<template>
  <div class="postlist">
    <!--Êï∞ÊçÆÊú™ËøîÂõûÊó∂ÊòæÁ§∫loading-->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif">
    </div>
    <!--‰∏ªÈ¢òÂàóË°®-->
    <div v-else>
      <ul>
        <li>
          <div class="topbar">
            <span @click="change" :class="[tab === '' ? 'active':'deactive']">ÂÖ®ÈÉ®</span>
            <span @click="change" :class="[tab === 'good' ? 'active':'deactive']">Á≤æÂçé</span>
            <span @click="change" :class="[tab === 'share' ? 'active':'deactive']">ÂàÜ‰∫´</span>
            <span @click="change" :class="[tab === 'ask' ? 'active':'deactive']">ÈóÆÁ≠î</span>
            <span @click="change" :class="[tab === 'job' ? 'active':'deactive']">ÊãõËÅò</span>
          </div>
        </li>
        <li v-for="post in posts">
          <!--Â§¥ÂÉè-->
          <router-link :to="{
          name:'user_info',
          params:{
          name:post.author.loginname
          }
          }">
            <img class="avatar" :src="post.author.avatar_url" alt="">
          </router-link>
          <!--ÂõûÂ§çÊµèËßàÊï∞-->
          <span class="count">
          <span class="reply_count">{{post.reply_count}}</span><span class="visit_count">/{{post.visit_count}}</span>
          </span>
          <!--ÂàÜÁ±ª-->
          <span :class="[{
          post_good:(post.good===true),
          post_top:(post.top===true),
          topiclist_tab:(post.good!=true&&post.top!=true)
          }]">{{post|tabFormatter}}</span>
          <!--Ê†áÈ¢ò-->
          <router-link :to="{
          name:'post_content',
          params:{
            id:post.id,
            name:post.author.loginname
          }}">
            <span>{{post.title}}</span>
          </router-link>
          <!--ÊúÄÂêéÂõûÂ§çÊó∂Èó¥-->
          <span class="last_reply">{{post.last_reply_at | formatDate}}</span>
          <!--ÂàÜÂâ≤Á∫ø-->
        </li>
        <!--È°µÁ†ÅË°®-->
        <li>
          <Pagination @handleList="renderList"></Pagination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Pagination from './Pagination'

  export default {
    name: "PostList",
    data() {
      return {
        isLoading: false,
        //È°µÈù¢ÂàóË°®Êï∞ÁªÑ
        posts: [],
        postpage: 1,
        tab: ''
      }
    },
    components: {
      Pagination
    },
    methods: {
      getData() {
        this.$http.get('https://cnodejs.org/api/v1/topics', {
          params: {
            page: this.postpage,
            limit: 20,
            tab: this.tab,
          }
        })
        //ËøîÂõûÊàêÂäü
          .then(res => {
            this.isLoading = false
            this.posts = res.data.data
          })
          //ËøîÂõûÂ§±Ë¥•
          .catch(err => {
            console.log(err)
          })
      },
      change(event) {
        if (event.currentTarget.innerText === 'ÂÖ®ÈÉ®') {
          this.tab = ''
        } else if (event.currentTarget.innerText === 'Á≤æÂçé') {
          this.tab = 'good'
        } else if (event.currentTarget.innerText === 'ÂàÜ‰∫´') {
          this.tab = 'share'
        } else if (event.currentTarget.innerText === 'ÈóÆÁ≠î') {
          this.tab = 'ask'
        } else if (event.currentTarget.innerText === 'ÊãõËÅò') {
          this.tab = 'job'
        } else {

        }

        this.getData()
      },
      renderList(value) {
        this.postpage = value;
        this.getData()
      }
    },
    beforeMount() {
      this.isLoading = true
      this.getData()
    }
  }
</script>

<style scoped>
  ul {
    padding: 0;
  }

  ul li:not(:first-child) {
    padding: 10px;
    font-size: 15px;
    font-weight: 400;
    border-top: 1px solid #f0f0f0;
  }

  li:hover {
    background-color: #E1E1E1;
  }

  .topbar {
    font-size: 14px;
  }

  .active {
    color: #ffffff;
    background-color: #80BD22;
    border-radius: 3px;
    padding: 3px 4px;
  }

  .deactive {
    color: #80BD22;
    padding: 10px;
  }

  .postlist {
    background-color: #fff;
    width: 90%;
    margin: 0 auto;
  }

  .topbar {
    background-color: #f6f6f6;
    padding: 10px;
    margin: 0;
  }

  .count {
    display: inline-block;
    width: 70px;
    color: #444444;
    text-align: center;
  }

  .reply_count {
    color: #9e78c0;
  }

  .visit_count {
    font-size: 10px;
    color: #444444;
  }

  .post_good, .post_top {
    background-color: #80BD22;
    color: #FFFFFF;
    border-radius: 3px;
    padding: 2px 4px;
    font-size: 12px;
    margin-right: 10px;
  }

  .topiclist_tab {
    background-color: #E5E5E5;
    color: #b4b4b4;
    border-radius: 3px;
    padding: 2px 4px;
    font-size: 12px;
    margin-right: 10px;
  }

  .last_reply {
    color: #b4b4b4;
    font-size: 12px;
    float: right;
    margin: 10px;
  }
</style>



