Vue.filter('tabFormatter', function (post) {
  if (post.good == true) {
    return 'Á≤æÂçé'
  } else if (post.top == true) {
    return 'ÁΩÆÈ°∂'
  } else if (post.tab == 'ask') {
    return 'ÈóÆÁ≠î'
  } else if (post.tab == 'share') {
    return 'ÂàÜ‰∫´'
  } else {
    return 'ÊãõËÅò'
  }
})



